.Chirp {
    max-width: 100%;
    width: 50rem;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    background-color: white; 
    padding: 0.7rem;
    border-radius: 1rem;
    margin-top: 1.5rem;
}

.Chirp .chirpimgholder {
    height: 100%;
    aspect-ratio: 1/1;
    border-radius: 100%;
    overflow: hidden;
    border-color: #8edbe6;
    border-style: solid;
    border-width: 0.2rem;
    background-color: #9deffa;
}

.Chirp .chirpimgholder img{
    max-width: 100%;
    max-height: 100%;
}

.Chirp .chirpcontent{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 1.5rem;
}

.Chirp .topchirp {
    height: 4rem;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
}

.Chirp .username{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 1.5rem;
}

.Chirp .middlechirp {
    flex-grow: 1;
    width: 100%;
}

.Chirp .bottomchirp {
    height: 3rem;
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
}

.Chirp .buttonholder{
    height: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
}

.Chirp .likecounter{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 1.2rem;
}

.Chirp .buttonholder img{
    max-height: 50%;
    aspect-ratio: 1/1;
    margin-left: 0.3rem;
    margin-right: 0.3rem;
}

.Chirp .buttonholder img:hover{
    cursor: pointer;
}