.App{
    display: flex;
    flex-flow: row nowrap;
    height: 100%;
    justify-content: center;
    align-items: flex-start;
}

.leftboxhome{
    width: 10rem;
    height: 100%;
    box-sizing: border-box;
    border-width: 2px;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
    min-width: 90px;
    overflow: auto;
    z-index: 1;
    box-shadow: rgb(0 0 0 / 8%) 0px 8px 28px;
}

.rightboxhome{
    width: 10rem;
    height: 100%;
    box-sizing: border-box;
    border-width: 2px;
    min-width: 87px;
    box-shadow: rgb(0 0 0 / 8%) 0px 8px 28px;
}

.middleboxhome{
    flex-grow: 1;
    height: 100%;
    box-sizing: border-box;
    display: flex;
    background-color: whitesmoke;
    overflow: auto;
    justify-content: flex-start;
    align-items: flex-start;
}

@media (max-width: 939px) {
    .leftboxhome{
        align-items: center;
        width: 100%;
        position: absolute;
        bottom: 0%;
        height: auto;
        padding: 0px;
    }
    .rightboxhome{
        display: none;
    }
}